.projects {
    text-align: center;
    margin-top: 500px;
}

.projects h2 {
    font-size: 300%;
    margin-bottom: 40px;
}

.project-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px; /* Adjust the gap as needed */
    margin-top: 20px; /* Adjust the top margin as needed */
}

.project-box {
    text-align: center;
    border: 2px solid white;
    // border-radius: 10px;
    width: 500px;
    padding: 20px; /* Add padding for better spacing */
    background-color: #0a0a0a; /* Add a background color */
    color: white; /* Set text color */
    box-shadow: 0 0 10px rgb(255, 255, 255); /* Add a subtle box shadow */
    transition: background-color 0.3s ease, border 0.3s ease, color 0.3s ease; /* Add transitions */
}

.project-box:hover {
    border: 3px solid orange;
    color: black; /* Change text color on hover */
    transition: background-color 0.3s ease, border 0.3s ease, color 0.3s ease; /* Add transitions */
}

.projects h3 {
    background-color: #0a0a0a;
    font-size: 170%;
}

.projects p{
    background-color: #0a0a0a;
    font-family: 'Poppins', sans-serif;
    color: white;
    font-weight: 250;
}

.projects a{
    background-color: #0a0a0a;
    color: palevioletred;
    font-family: 'Poppins', sans-serif;
}

.project a:hover {
    font-weight: bold;
}

@media (max-width: 768px) {
    .projects{
        margin-top: 300px;
    }
}
